import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

import '@servicenow/now-input';
import '@servicenow/now-typeahead';
import '@servicenow/now-checkbox';
import '@servicenow/now-text-link';
import hasMobileStudioAccess from '../../helpers/hasMobileStudioAccess';
import {
	tablesDataBehavior,
	rolesDataBehavior,
	userDataBehavior,
	runTemplateBehavior as runTemplateDefinition
} from '@devsnc/guided-wizard-toolkit';

import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

const {
	constants: {
		TABLES_DATA_PROPERTY,
		TABLES_DATA_IN_SCOPE_PROPERTY,
		TABLES_DATA_LOADING_PROPERTY
	}
} = tablesDataBehavior;
const {
	constants: {
		ROLES_DATA_PROPERTY,
		ROLES_DATA_IN_SCOPE_PROPERTY,
		ROLES_DATA_LOADING_PROPERTY
	}
} = rolesDataBehavior;
const {
	constants: { USER_DATA_PROPERTY }
} = userDataBehavior;
const {
	behavior: runTemplateBehavior,
	helpers: { runTemplate, isInProgress },
	actionTypes: { RUN_TEMPLATE_SUCCESS }
} = runTemplateDefinition;

import styles from './sn-mobile-details-page.scss';
import mobileUiSVG from './assets/mobile_ui.png';
import {
	PREPARE_RUN_TEMPLATE,
	PREVENT_ONLY_WHITESPACE_REGEX,
	FETCH_MOBILE_OFFLINE,
	FETCH_MOBILE_OFFLINE_SUCCESS,
	FETCH_MOBILE_OFFLINE_FAILURE
} from './constants';
import progressSVG from '../../../assets/progress-tables.svg';

const getSubheaderText = hasMobileAccess =>
	hasMobileAccess
		? t(
				"This will add a default mobile experience of your app's content to your company's iOS or Android app. You can further edit or enhance this experience in Mobile Studio, later."
		  )
		: t(
				"This will add your app's content to your company's iOS or Android app. After you set up the mobile experience, you will need to work with your admin on any further configuration."
		  );

createCustomElement('sn-mobile-details-page', {
	view(state, helpers) {
		const {
			[TABLES_DATA_PROPERTY]: allTables,
			[TABLES_DATA_LOADING_PROPERTY]: isTablesLoading,
			[ROLES_DATA_PROPERTY]: allRoles,
			[ROLES_DATA_LOADING_PROPERTY]: isRolesLoading,
			[USER_DATA_PROPERTY]: user,
			currentInstanceAesConfig
		} = state.properties;
		const { dispatch } = helpers;

		const hasMobileAccess = hasMobileStudioAccess(currentInstanceAesConfig);
		const subheaderText = getSubheaderText(hasMobileAccess);

		const isPrimaryDisabled =
			state.isFormInvalid || state.tables.length === 0 || isInProgress(state);

		const isLoading = isInProgress(state);
		const loadingContent = {
			headerText: t(
				"Ok, {0}. We're adding this mobile experience to your app.",
				user.firstName
			),
			subheaderText: t('This may take a few moments...'),
			image: progressSVG
		};

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => dispatch(PREPARE_RUN_TEMPLATE)}
				primaryDisabled={isPrimaryDisabled}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				loading={isLoading}
				loading-content={loadingContent}
				max-width="744px">
				<div className="custom-header" slot="SN_GUIDED_WIZARD_PAGE#HEADER_SLOT">
					<h2>{t('ADD EXPERIENCE')}</h2>
					<h1>
						{isLoading
							? loadingContent.headerText
							: t(`Lets set up a mobile experience for your app`)}
					</h1>
					<span>
						{isLoading ? loadingContent.subheaderText : subheaderText}{' '}
						{hasMobileAccess && !isLoading ? (
							<now-text-link
								slot="SN_GUIDED_WIZARD_PAGE#FOOTER_SLOT"
								className="text-link"
								label={t('Learn about Mobile Studio.')}
								href="/api/now/v1/context_doc_url/CSHelp:Agent-Config"
								opensWindow
							/>
						) : null}
					</span>
				</div>
				<p className="details-preface now-m-block-end--3xl">
					<b>{t(`Review and update, if necessary.`)}</b>&nbsp;
					{t(
						`We copied over information from your app. Any changes made here will only affect the mobile experience. This won't affect the app you're creating.`
					)}
				</p>
				<div className="details-main now-align -inline now-m-block-end--3xl">
					<div className="details-left now-align -column">
						<now-input
							className="name now-m-block-end--xxl"
							name="name"
							label={t('Name')}
							value={state.name}
							manage-value
							helperContent={generateStyleToolTipContent(
								t('User-facing name of the mobile application.')
							)}
							size="lg"
							autocomplete="off"
							maxlength={100}
							required
							pattern={PREVENT_ONLY_WHITESPACE_REGEX}
						/>

						<now-input
							className="description now-m-block-end--xxl"
							name="short_description"
							label={t('Description')}
							value={state.short_description}
							manage-value
							helperContent={generateStyleToolTipContent(
								t('Short description of what this application does.')
							)}
							size="lg"
							autocomplete="off"
							maxlength={500}
						/>
						<sn-aow-typeahead
							className="tables now-m-block-end--xxl"
							name="tables"
							label={t('Tables')}
							placeholder={isTablesLoading ? t('Loading...') : ''}
							helperContent={generateStyleToolTipContent(
								t(
									'Each data table creates an applet that shows detailed lists and records.'
								)
							)}
							disabled={isTablesLoading}
							items={allTables}
							selected-items={isTablesLoading ? [] : state.tables}
							manage-selected-items
							variant="tertiary"
							search="contains"
							required
							itemsLimit={100}
							multi
						/>

						<now-typeahead-multi
							className="roles now-m-block-end--xxl"
							name="user_roles"
							label={t('Roles')}
							placeholder={isRolesLoading ? t('Loading...') : ''}
							helperContent={generateStyleToolTipContent(
								t(
									"Specify who can access this experience by choosing one or more roles. If you want to make this accessible to everyone, leave the field blank. If you don't see a role you'd like in this list, go to Security to create a custom role and restart the mobile set up process again."
								)
							)}
							disabled={isRolesLoading}
							items={allRoles}
							selected-items={isRolesLoading ? [] : state.user_roles}
							manage-selected-items
							variant="tertiary"
							search="contains"
						/>

						{state.isSgOfflinePluginActive ? (
							<sn-aow-checkbox-tooltip
								helperContent={generateStyleToolTipContent(
									t(
										'Lets the user work on data without an internet connection. The data updates once the phone is connected to the internet again.'
									)
								)}>
								<now-checkbox
									slot="AOW_CHECKBOX_TOOLTIP#CHECKBOX_SLOT"
									className="offline now-m-block-end--xxl"
									name="available_offline"
									label={t('Available offline')}
									checked={state.available_offline}
									manage-checked
								/>
							</sn-aow-checkbox-tooltip>
						) : null}
					</div>

					<img
						className="details-right now-m-inline-start--3xl"
						src={mobileUiSVG}
						alt=""
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},

	initialState: {
		name: '',
		short_description: '',
		tables: [],
		user_roles: [],
		available_offline: false,
		isFormInvalid: true,
		isSgOfflinePluginActive: false
	},

	properties: {
		wizardContext: { default: {} },
		[TABLES_DATA_PROPERTY]: {
			default: `@mobileWizard/${TABLES_DATA_PROPERTY}`
		},
		[TABLES_DATA_IN_SCOPE_PROPERTY]: {
			default: `@mobileWizard/${TABLES_DATA_IN_SCOPE_PROPERTY}`
		},
		[TABLES_DATA_LOADING_PROPERTY]: {
			default: `@mobileWizard/${TABLES_DATA_LOADING_PROPERTY}`
		},
		[ROLES_DATA_PROPERTY]: {
			default: `@mobileWizard/${ROLES_DATA_PROPERTY}`
		},
		[ROLES_DATA_IN_SCOPE_PROPERTY]: {
			default: `@mobileWizard/${ROLES_DATA_IN_SCOPE_PROPERTY}`
		},
		[ROLES_DATA_LOADING_PROPERTY]: {
			default: `@mobileWizard/${ROLES_DATA_LOADING_PROPERTY}`
		},
		[ROLES_DATA_LOADING_PROPERTY]: {
			default: `@mobileWizard/${ROLES_DATA_LOADING_PROPERTY}`
		},
		[USER_DATA_PROPERTY]: {
			default: `@mobileWizard/${USER_DATA_PROPERTY}`
		},
		currentInstanceAesConfig: {
			default: '@sn-aes-provider/currentInstanceAesConfig'
		}
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties,
			dispatch,
			updateState
		}) => {
			const { app } = properties.wizardContext;

			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-mobile-details-page'
			});

			updateState({ name: app.name });
			dispatch(FETCH_MOBILE_OFFLINE);
		},

		// Prepopulate tables/roles options with in-scope records
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			state,
			action,
			updateState
		}) => {
			const {
				payload: { value, name }
			} = action;
			const { tables, user_roles } = state;
			if (name === ROLES_DATA_IN_SCOPE_PROPERTY && user_roles.length === 0) {
				updateState({ user_roles: value });
			}
			if (name === TABLES_DATA_IN_SCOPE_PROPERTY && tables.length === 0) {
				updateState({ tables: value });
			}
		},

		['NOW_INPUT#VALUE_SET']: formControlUpdateEffect,
		['NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET']: formControlUpdateEffect,
		['NOW_CHECKBOX#CHECKED_SET']: formControlUpdateEffect,
		['NOW_INPUT#INVALID_SET']: formValidityUpdateEffect,
		['NOW_TYPEAHEAD_MULTI#INVALID_SET']: formValidityUpdateEffect,

		[FETCH_MOBILE_OFFLINE]: createHttpEffect(
			'/api/sn_app_eng_studio/plugin_activations/mobile_offline_active',
			{
				method: 'GET',
				successActionType: FETCH_MOBILE_OFFLINE_SUCCESS,
				errorActionType: FETCH_MOBILE_OFFLINE_FAILURE,
				batch: false
			}
		),

		[FETCH_MOBILE_OFFLINE_SUCCESS]: ({ action, updateState }) => {
			const { active } = action.payload.result;
			updateState({ isSgOfflinePluginActive: active });
		},

		[FETCH_MOBILE_OFFLINE_FAILURE]: ({ action }) => {
			console.error(action.payload.message);
		},

		[PREPARE_RUN_TEMPLATE]: ({ state, properties, dispatch }) => {
			const { app, templateId } = properties.wizardContext;
			const {
				name,
				short_description,
				tables,
				user_roles,
				available_offline
			} = state;
			const variables = {
				name,
				short_description,
				available_offline,
				app_id: app.sys_id,
				app_name: app.name,
				tables: serializeByProp(tables, 'id'),
				user_roles: serializeByProp(user_roles, 'label')
			};

			runTemplate(dispatch, templateId, variables, {
				sysparm_transaction_scope: app.sys_id
			});
		},

		[RUN_TEMPLATE_SUCCESS]: ({ action, dispatch, state }) => {
			dispatch('SN_GUIDED_WIZARD#CONTINUE', {
				mobileTemplateResult: action.payload.result,
				mobileTemplateVariables: {
					name: state.name
				}
			});
		}
	},

	behaviors: [runTemplateBehavior],

	styles
});

function formControlUpdateEffect({ action, updateState }) {
	const { name, value } = action.payload;

	updateState({
		operation: 'set',
		path: name,
		value
	});
}

function formValidityUpdateEffect({ state, action, updateState }) {
	const { isFormInvalid } = state;
	const { name, value: isInvalid } = action.payload;

	if (isFormInvalid !== isInvalid && (name === 'name' || name === 'tables')) {
		updateState({ isFormInvalid: isInvalid });
	}
}

function serializeByProp(collection, prop) {
	return collection.map(item => item[prop]).join(',');
}
