import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

import '../../../components/sn-aot-tile-flow';
import authToken from '../../../helpers/get-auth-token';

import dataSVG from './assets/data.svg';
import experienceSVG from './assets/experience.svg';
import flowSVG from './assets/flow.svg';
import lockSVG from './assets/lock.svg';

import isScopedAppTemplate from '../../helpers/isScopedAppTemplate';

import styles from './sn-application-summary-page.scss';

const FETCH_APP_AND_CONTINUE = 'SN_APP_WIZARD#FETCH_APP';
const FETCH_APP_SUCCESS = 'SN_APP_WIZARD#FETCH_APP_SUCCESS';
const FETCH_APP_FAILURE = 'SN_APP_WIZARD#FETCH_APP_FAILURE';

const POST_APP_SCOPE_TO_INSTANCE = 'POST_APP_SCOPE_TO_INSTANCE';

const postAppScopeToInstance = createHttpEffect(
	'/api/now/ui/concoursepicker/application',
	{
		method: 'PUT',
		headers: {
			'Content-Type': 'application/json',
			'X-WantSessionNotificationMessages': true,
			...authToken
		},
		batch: false,
		dataParam: 'app_request'
	}
);

const headerTextCopy = {
	scratch: t("Great! Let's add more to your app"),
	template: t("Great! Let's review what's in your app")
};

const subheaderTextCopy = {
	scratch: t(
		"The following elements are optional. Not every app needs them. Once you're done adding what you need, you can submit your app to your administrator for review."
	),
	template: t(
		"Next, you'll be able to review and modify everything that came with the template. Once you're done, you can submit your app to your administrator for review."
	)
};

createCustomElement('sn-application-summary-page', {
	view: ({ properties: { wizardContext } }, { dispatch }) => {
		const { app, templateId, frontendComposed = false } = wizardContext;

		if (typeof app === 'undefined') {
			return null;
		}

		const textCondition = isScopedAppTemplate(templateId)
			? 'scratch'
			: 'template';

		const onComplete = () =>
			dispatch(FETCH_APP_AND_CONTINUE, {
				id: app.sys_id
			});

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onComplete}
				primary-label={
					frontendComposed ? t('Continue') : t('Go to app dashboard')
				}
				onClose={
					frontendComposed
						? () => {
								dispatch('SN_GUIDED_WIZARD#CANCEL');
						  }
						: onComplete
				}
				runningHeaderText={t('CREATE APP')}
				headerText={headerTextCopy[textCondition]}
				subheaderText={subheaderTextCopy[textCondition]}
				max-width="620px">
				<sn-aot-tile-flow
					items={[
						{
							imgSrc: dataSVG,
							label: t('Data')
						},
						{
							imgSrc: experienceSVG,
							label: t('Experience')
						},
						{
							imgSrc: flowSVG,
							label: t('Automation')
						},
						{
							imgSrc: lockSVG,
							label: t('Security')
						}
					]}
				/>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch
		}) => {
			dispatch(POST_APP_SCOPE_TO_INSTANCE, {
				app_request: { app_id: wizardContext.app.sys_id }
			});
			dispatch('SYS_APP_WIZARD#APP_CREATED', {
				app: wizardContext.app
			});
		},
		POST_APP_SCOPE_TO_INSTANCE: postAppScopeToInstance,
		[FETCH_APP_AND_CONTINUE]: createHttpEffect('/api/now/table/sys_app/:id', {
			method: 'GET',
			successActionType: FETCH_APP_SUCCESS,
			errorActionType: FETCH_APP_FAILURE,
			pathParams: ['id'],
			batch: false
		}),
		[FETCH_APP_SUCCESS]: ({ action, dispatch }) => {
			// When wizard is completed, fetch the full app info
			// to send to template manager in the case it's a composed wizard
			const app = action.payload.result;
			dispatch('SN_GUIDED_WIZARD#COMPLETE', {
				key: 'sys_app',
				result: app
			});
		},
		[FETCH_APP_FAILURE]: ({ action, properties, dispatch }) => {
			console.warn(
				t('Unable to retrieve app information'),
				action.payload.result
			);
			dispatch('SN_GUIDED_WIZARD#COMPLETE', {
				key: 'sys_app',
				result: properties.wizardContext.app
			});
		}
	},
	styles
});
