import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';

// Page imports
import './pages/sn-security-choose-source-page';
import './pages/sn-security-choose-existing-role-page';
import './pages/sn-security-role-input-page';
import './pages/sn-security-define-acl-page';
import './pages/sn-security-summary-page';
import { normalizeObjectFiles } from '../helpers/normalizeObjectFiles';

// Security Flow Definition
const CHOOSE_SOURCE_PAGE = 'sn-security-choose-source-page';
const CHOOSE_EXISTING_ROLE_PAGE = 'sn-security-choose-existing-role-page';
const DEFINE_ACL_PAGE = 'sn-security-define-acl-page';
const ROLE_INPUT_PAGE = 'sn-security-role-input-page';
const ROLE_SUMMARY_PAGE = 'sn-security-summary-page';

const pageSource = {
	new: ROLE_INPUT_PAGE,
	existing: CHOOSE_EXISTING_ROLE_PAGE
};

export const securityFlow = {
	startPage: CHOOSE_SOURCE_PAGE,
	pages: {
		[CHOOSE_SOURCE_PAGE]: {
			continue: ({ source }) => pageSource[source]
		},
		[CHOOSE_EXISTING_ROLE_PAGE]: {
			continue: () => DEFINE_ACL_PAGE,
			previous: () => CHOOSE_SOURCE_PAGE
		},
		[ROLE_INPUT_PAGE]: {
			continue: () => DEFINE_ACL_PAGE,
			previous: () => CHOOSE_SOURCE_PAGE
		},
		[DEFINE_ACL_PAGE]: {
			continue: () => ROLE_SUMMARY_PAGE,
			previous: ({ source }) => pageSource[source]
		},
		[ROLE_SUMMARY_PAGE]: {
			continue: () => {},
			previous: () => DEFINE_ACL_PAGE
		}
	}
};
// End Security Flow Definition

const STEPPER_ITEMS = {
	[CHOOSE_SOURCE_PAGE]: t('Basic info'),
	[DEFINE_ACL_PAGE]: t('Configure'),
	[ROLE_SUMMARY_PAGE]: t('Summary')
};

createCustomElement('sn-aes-security-template-wizard', {
	view: state => {
		const {
			appObjectFactory,
			disableStepper,
			appHasValidObjects,
			properties
		} = state;
		const { wizardContext, stepperItems, templateId, app } = properties;
		const PageComponent = getCurrentPage(wizardContext);

		return (
			<sn-guided-wizard
				flowDefinition={securityFlow}
				component-id="securityWizard"
				stepper-items={disableStepper ? [] : stepperItems}
				initial-context={{
					templateId,
					app
				}}
				roles-data-options={{
					appId: app.sys_id,
					fetchOnBootstrap: true,
					normalizeResult: result => {
						return result.map(role => {
							return {
								id: role.sys_id,
								label: role.name,
								description: role.description
							};
						});
					}
				}}>
				<PageComponent
					wizard-context={wizardContext}
					appObjectFactory={appObjectFactory}
					appHasValidObjects={appHasValidObjects}
				/>
			</sn-guided-wizard>
		);
	},

	initialState: {
		appObjectFactory: {},
		appHasValidObjects: false,
		disableStepper: true
	},

	properties: {
		wizardContext: { default: '@securityWizard/wizardContext' },
		templateId: { default: null },
		app: { default: {} }
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch }) => {
			const { sys_id: appId } = properties.app;
			dispatch('FETCH_APP_OBJECTS', { appId });
		},

		DISABLE_STEPPER: ({ updateState }) => {
			updateState({
				disableStepper: true
			});
		},

		ENABLE_STEPPER: ({ updateState }) => {
			updateState({
				disableStepper: false
			});
		},

		FETCH_APP_OBJECTS: createHttpEffect(
			`/api/sn_app_eng_studio/applications/:appId`,
			{
				method: 'GET',
				batch: false,
				pathParams: ['appId'],
				successActionType: 'FETCH_APP_OBJECTS_SUCCESS',
				errorActionType: 'FETCH_APP_OBJECTS_FAILURE'
			}
		),

		FETCH_APP_OBJECTS_SUCCESS: ({ action, updateState }) => {
			const { result } = action.payload;
			const files = normalizeObjectFiles(result);

			updateState({
				appObjectFactory: {
					...result,
					files
				},
				appHasValidObjects: files.length > 0
			});
		},

		FETCH_APP_OBJECTS_FAILURE: ({ action }) => {
			console.warn(action.payload.message);
		}
	},

	behaviors: [
		{
			behavior: stepperBehavior.behavior,
			options: {
				config: STEPPER_ITEMS
			}
		}
	]
});
