import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-checkbox';
import '@devsnc/guided-wizard-toolkit';

import {
	UX_PAGE_ACL_TYPE,
	UX_ROUTE_ACL_TYPE,
	TAB_ACLS_SET,
	ACCESS_SELECT_ALL_ACLS,
	ACCESS_REMOVE_ALL_ACLS,
	ACCESS_SELECT_FILE_ACL,
	ACCESS_REMOVE_FILE_ACL
} from './constants';

import { createAcl, getAllCheckedStatus, transformUrlPath } from './helpers';

import styles from './tab-panel-shared.scss';

const getAllDefinitionsCheckedStatus = aclDefinitions => {
	let checkedCount = 0;

	for (const aclDefinition of aclDefinitions) {
		checkedCount += aclDefinition.acls.length;
	}

	return getAllCheckedStatus(checkedCount, aclDefinitions.length * 2);
};

function createPageRegistryAcls(aclName) {
	return [
		createAcl(aclName, UX_PAGE_ACL_TYPE, 'read'),
		createAcl(`${aclName}.*`, UX_ROUTE_ACL_TYPE, 'read')
	];
}

// So, this whole component is now hard-coded to work for sys_ux_page_registry
// Along with... the rest of the wizard
function castAccessAclDefinition(objectFile, createAcls) {
	const sys_name = transformUrlPath(objectFile.launch_url);
	return {
		id: objectFile.sys_id,
		name: objectFile.name,
		sys_name,
		...(createAcls
			? {
					acls: createPageRegistryAcls(sys_name)
			  }
			: {
					acls: []
			  })
	};
}

createCustomElement('sn-security-access-acl-tab-panel', {
	view(state, { dispatch }) {
		const { aclDefinitions, properties } = state;
		const { tabName, tabLabel } = properties;

		// To keep data congruent and not iterate over everything a second time,
		// collect the raw acls while rendering and dispatching that to sn-security-define-acl-page
		dispatch(TAB_ACLS_SET, { tabName, aclDefinitions });

		return (
			<ul className={`object-list object-list--access`}>
				{aclDefinitions.length > 0 ? (
					<li className="object-list-item -header">
						<div className="item-cell -name">
							<now-checkbox
								checked={getAllDefinitionsCheckedStatus(aclDefinitions)}
								manage-checked
								config-aria={{
									'aria-label': t('Grant all ACLs to {0}', tabLabel)
								}}
								append-to-payload={{
									operation: 'allAcls'
								}}
							/>
							{t('Select All')}
						</div>
					</li>
				) : (
					<p>
						{t(
							'You cannot configure these permissions until you add an experience to this app.'
						)}
					</p>
				)}

				{aclDefinitions.map((aclDefinition, index) => {
					const aclPayload = {
						aclDefinition,
						index
					};

					return (
						<li
							key={aclDefinition.id}
							className="object-list-item"
							sys-id={aclDefinition.id}>
							<div className="item-cell -name">
								<now-checkbox
									checked={aclDefinition.acls.length > 0}
									manage-checked
									config-aria={{
										'aria-label': t('Grant access to {0}', aclDefinition.name)
									}}
									append-to-payload={{
										...aclPayload,
										operation: 'grantAccess'
									}}
								/>
								{aclDefinition.name}
							</div>
						</li>
					);
				})}
			</ul>
		);
	},

	initialState: {
		aclDefinitions: []
	},

	properties: {
		tabName: { default: null },
		tabLabel: { default: null },
		objectFiles: { default: [] },

		// Strictly used on component connected to retain tab state when tabbing away
		aclDefinitions: { default: [] }
	},

	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: ({
			properties,
			dispatch,
			updateState
		}) => {
			const { aclDefinitions } = properties;

			if (aclDefinitions.length > 0) {
				updateState({ aclDefinitions });
			} else {
				dispatch(ACCESS_REMOVE_ALL_ACLS);
			}
		},

		['NOW_CHECKBOX#CHECKED_SET']: ({ action, dispatch }) => {
			action.stopPropagation();

			const { value: checked, operation } = action.payload;
			const aliasAction = actionType =>
				dispatch(actionType, action.payload, action.meta);

			switch (operation) {
				case 'allAcls':
					aliasAction(
						checked ? ACCESS_SELECT_ALL_ACLS : ACCESS_REMOVE_ALL_ACLS
					);
					break;

				default:
					aliasAction(
						checked ? ACCESS_SELECT_FILE_ACL : ACCESS_REMOVE_FILE_ACL
					);
					break;
			}
		},

		[ACCESS_SELECT_ALL_ACLS]: ({ properties, updateState }) => {
			const { objectFiles } = properties;
			const aclDefinitions = objectFiles.map(file =>
				castAccessAclDefinition(file, true)
			);
			updateState({ aclDefinitions });
		},

		[ACCESS_REMOVE_ALL_ACLS]: ({ properties, updateState }) => {
			const { objectFiles } = properties;
			const aclDefinitions = objectFiles.map(file =>
				castAccessAclDefinition(file)
			);
			updateState({ aclDefinitions });
		},

		[ACCESS_SELECT_FILE_ACL]: ({ action, updateState }) => {
			const {
				index,
				aclDefinition: { sys_name }
			} = action.payload;

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}].acls`,
				value: createPageRegistryAcls(sys_name)
			});
		},

		[ACCESS_REMOVE_FILE_ACL]: ({ action, updateState }) => {
			const { index } = action.payload;

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}].acls`,
				value: []
			});
		}
	},

	styles
});
