import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

import '@servicenow/now-tabs';
import '@servicenow/now-loader';

import { runTemplateBehavior } from '@devsnc/guided-wizard-toolkit';
const {
	helpers: { runTemplate, isInProgress },
	actionTypes: { RUN_TEMPLATE_SUCCESS }
} = runTemplateBehavior;

import defineAclEmptySVG from '../../../assets/define-acl-empty.svg';
import styles from './sn-security-define-acl-page.scss';
import './sn-security-crud-acl-tab-panel';
import './sn-security-access-acl-tab-panel';

import {
	PREPARE_TEMPLATE_DATA,
	TAB_ACLS_SET,
	DATA_TAB,
	EXPERIENCE_TAB,
	DATA_ACL_TYPE,
	DATA_TAXONOMY_CATEGORY_ID,
	EXPERIENCE_TAXONOMY_CATEGORY_ID
} from './constants';

import { flatten, extractAclsFromDefinition } from './helpers';

const TAB_PANELS = {
	[DATA_TAB]: {
		TabPanel: 'sn-security-crud-acl-tab-panel',
		aclType: DATA_ACL_TYPE,
		categoryId: DATA_TAXONOMY_CATEGORY_ID
	},

	[EXPERIENCE_TAB]: {
		TabPanel: 'sn-security-access-acl-tab-panel',
		categoryId: EXPERIENCE_TAXONOMY_CATEGORY_ID
	}
};

function getObjectFiles(appObjectFactory, categoryId) {
	return appObjectFactory.files.filter(
		file => file.taxonomy_category === categoryId
	);
}

function extractAclsFromTabAclDefinitions(tabAclDefinitions) {
	const mixedAclDefinitions = flatten(Object.values(tabAclDefinitions));
	return flatten(mixedAclDefinitions, extractAclsFromDefinition);
}

createCustomElement('sn-security-define-acl-page', {
	view(state, helpers) {
		const { dispatch } = helpers;
		const {
			tabItems,
			selectedTab,
			tabAclDefinitions,
			definedAclsCount,
			properties
		} = state;
		const {
			appObjectFactory,
			appHasValidObjects,
			wizardContext: { role }
		} = properties;
		const { TabPanel, aclType, categoryId } = TAB_PANELS[selectedTab];
		const aclDefinitions = tabAclDefinitions[selectedTab];
		const tabLabel = tabItems.find(({ id }) => id === selectedTab);

		const isPrimaryDisabled =
			isInProgress(state) || (role.sys_id ? definedAclsCount === 0 : false);

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => dispatch(PREPARE_TEMPLATE_DATA)}
				primaryDisabled={isPrimaryDisabled}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				runningHeaderText={t('ADD SECURITY')}
				headerText={t(`What can the '{0}' role do within your app?`, role.name)}
				subheaderText={t(
					'Choose how much control this role has over elements of your app.'
				)}
				maxWidth="680px">
				{appHasValidObjects ? (
					<Fragment>
						<now-tabs
							items={tabItems}
							selectedItem={selectedTab}
							manageSelectedItem
						/>
						<TabPanel
							className="tab-panel"
							tabName={selectedTab}
							tabLabel={tabLabel}
							aclType={aclType}
							aclDefinitions={aclDefinitions}
							objectFiles={getObjectFiles(appObjectFactory, categoryId)}
						/>
					</Fragment>
				) : (
					<div className="empty-state">
						<p>
							{t(
								'You cannot configure these permissions until you add data or an experience to the app.'
							)}
						</p>
						<img src={defineAclEmptySVG} alt="" />
					</div>
				)}
			</sn-guided-wizard-page>
		);
	},

	initialState: {
		tabItems: [],
		selectedTab: DATA_TAB,
		tabAclDefinitions: {},
		definedAclsCount: 0
	},

	properties: {
		appObjectFactory: { default: {} },
		appHasValidObjects: { default: false },
		wizardContext: { default: {} }
	},

	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties,
			dispatch,
			updateState
		}) => {
			const { taxonomy_categories } = properties.appObjectFactory;

			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-security-define-acl-page'
			});

			const getTaxonomyCategoryLabel = tc =>
				taxonomy_categories.find(({ sys_id }) => sys_id === tc).name;

			updateState({
				operation: 'set',
				path: `tabItems`,
				value: [
					{
						id: DATA_TAB,
						label: getTaxonomyCategoryLabel(DATA_TAXONOMY_CATEGORY_ID)
					},
					{
						id: EXPERIENCE_TAB,
						label: getTaxonomyCategoryLabel(EXPERIENCE_TAXONOMY_CATEGORY_ID)
					}
				]
			});
		},

		[TAB_ACLS_SET]: ({ state, action, updateState }) => {
			const { tabName, aclDefinitions } = action.payload;
			const { tabAclDefinitions, definedAclsCount } = state;
			const acls = extractAclsFromTabAclDefinitions({
				...tabAclDefinitions,
				[tabName]: aclDefinitions
			});

			if (definedAclsCount !== acls.length) {
				updateState({
					operation: 'set',
					path: 'definedAclsCount',
					value: acls.length
				});
			}

			updateState({
				operation: 'set',
				path: `tabAclDefinitions.${tabName}`,
				value: aclDefinitions,
				shouldRender: false
			});
		},

		[PREPARE_TEMPLATE_DATA]: ({
			properties: { wizardContext },
			state: { tabAclDefinitions },
			dispatch
		}) => {
			const {
				app: { sys_id: sysparm_transaction_scope },
				templateId: template_sys_id,
				role
			} = wizardContext;

			// Flatten tabAclDefinitions
			const acls = extractAclsFromTabAclDefinitions(tabAclDefinitions);

			runTemplate(
				dispatch,
				template_sys_id,
				{ role, acls },
				{ sysparm_transaction_scope }
			);
		},

		[RUN_TEMPLATE_SUCCESS]: ({ action, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTINUE', {
				securityTemplateResult: action.payload.result
			});
		},

		['NOW_TABS#SELECTED_ITEM_SET']: ({ action, updateState }) => {
			action.stopPropagation();
			const { value: selectedTab } = action.payload;
			updateState({ selectedTab });
		}
	},

	behaviors: [runTemplateBehavior],
	styles
});
