import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-loader';
import '@servicenow/now-typeahead';

import { rolesDataBehavior } from '@devsnc/guided-wizard-toolkit';
const {
	constants: { ROLES_DATA_PROPERTY }
} = rolesDataBehavior;

import styles from './sn-security-choose-existing-role-page.scss';
import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

createCustomElement('sn-security-choose-existing-role-page', {
	view: (
		{
			selectedRole: { id: sys_id, label: suffix, description },
			properties: { [ROLES_DATA_PROPERTY]: existingRoles }
		},
		{ dispatch }
	) => {
		return (
			<sn-guided-wizard-page
				primary-label={t('Edit')}
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#EDIT_RECORD', {
						key: 'sys_user_role',
						result: { sys_id, suffix, description }
					});
				}}
				primary-disabled={!sys_id}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onPrevious={() => {
					dispatch('SN_GUIDED_WIZARD#PREVIOUS');
				}}
				running-header-text={t('ADD SECURITY')}
				header-text={t(`Let's add an existing role`)}
				subheader-text={t('Select from existing roles.')}
				max-width="680px">
				<now-typeahead
					label={t('Roles')}
					helperContent={generateStyleToolTipContent(
						t(
							'These roles have already been built and assigned by your organization.'
						)
					)}
					className="roles"
					items={existingRoles}
					selectedItem={sys_id}
					variant="tertiary"
					placeholder={t('Search for a role')}
					search="contains"
				/>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		selectedRole: { id: '' }
	},
	properties: {
		wizardContext: {},
		[ROLES_DATA_PROPERTY]: {
			default: `@securityWizard/${ROLES_DATA_PROPERTY}`
		}
	},
	actionHandlers: {
		'NOW_TYPEAHEAD#SELECTED_ITEM_SET': ({
			properties: { [ROLES_DATA_PROPERTY]: existingRoles },
			action: {
				payload: { value },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();

			updateState({
				selectedRole: existingRoles.find(({ id }) => id === value)
			});
		}
	},
	styles
});
