import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';
import newRoleSVG from '../../../assets/newrole.svg';
import existingRoleSVG from '../../../assets/roles.svg';

import { rolesDataBehavior } from '@devsnc/guided-wizard-toolkit';
const {
	constants: { ROLES_DATA_PROPERTY }
} = rolesDataBehavior;

import styles from './sn-security-choose-source-page.scss';

createCustomElement('sn-security-choose-source-page', {
	view: (state, { dispatch }) => {
		const {
			appHasValidObjects,
			[ROLES_DATA_PROPERTY]: allRoles = []
		} = state.properties;
		const userHasRoles = allRoles.length > 0;
		const existingRolesDisabled = !appHasValidObjects || !userHasRoles;
		const existingRoleHelperContent = (() => {
			if (!userHasRoles) {
				return t('You do not have access to any roles at this time.');
			}

			if (!appHasValidObjects) {
				return t('Add experience or data to use an existing role.');
			}

			return t('Grant an existing role access to experience or data.');
		})();

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						source: state.source
					});
				}}
				primaryDisabled={!state.source}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('ADD SECURITY')}
				headerText={t('How do you want to add a role to your app?')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				maxWidth="680px">
				<div className="body-container">
					<sn-aow-tile-button
						className="new"
						id="new"
						icon={newRoleSVG}
						text={t('Build a new role')}
						isSelected={state.source === 'new'}
						helperContent={generateStyleToolTipContent(
							t('Make sure to assign this role to teammates once you build it.')
						)}
					/>
					<sn-aow-tile-button
						disabled={existingRolesDisabled}
						className="existing"
						id="existing"
						icon={existingRoleSVG}
						text={t('Use an existing role')}
						isSelected={state.source === 'existing'}
						helperContent={generateStyleToolTipContent(
							existingRoleHelperContent
						)}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},

	initialState: {
		source: ''
	},

	properties: {
		appHasValidObjects: { default: false },
		wizardContext: {},
		[ROLES_DATA_PROPERTY]: {
			default: `@securityWizard/${ROLES_DATA_PROPERTY}`
		}
	},

	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-security-choose-source-page'
			});
			dispatch('DISABLE_STEPPER');
		},
		['AOW_TILE_BUTTON#CLICKED']: ({
			action: {
				stopPropagation,
				payload: { id }
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ source: id });
		}
	},
	styles
});
