export const EXPERIENCE_TAXONOMY_CATEGORY_ID =
	'db5663577703330001fb4311a81061b9';
export const DATA_TAXONOMY_CATEGORY_ID = 'dd66a7577703330001fb4311a810611e';

// Just trying to keep all the dragons in the same cage...

/**
 * Returns the `sys_id` of the taxonomy_definition that corresponds to a `sys_ux_page_registry`
 * @param {{sys_id: string, table_name: string}[]} taxonomyDefinitions
 * @returns {string}
 */
function getPageRegistryTaxonomyId(taxonomyDefinitions) {
	if (!Array.isArray(taxonomyDefinitions)) {
		return null;
	}

	const pageRegistryTaxonomy = taxonomyDefinitions.find(
		({ table_name }) => table_name === 'sys_ux_page_registry'
	);

	return pageRegistryTaxonomy ? pageRegistryTaxonomy.sys_id : null;
}

/**
 * Returns only the files necessary for the security wizard
 * @param {object} appObjectFactory
 * @param {{ sys_id:string, taxonomy_category:string, taxonomy_definition:string, name_value: string }[]} appObjectFactory.files
 * @param {{sys_id: string, table_name: string}[]} appObjectFactory.taxonomy_definitions
 */
export function normalizeObjectFiles(appObjectFactory) {
	const { files, taxonomy_definitions } = appObjectFactory;

	const pageRegistryTaxonomyId = getPageRegistryTaxonomyId(
		taxonomy_definitions
	);

	return files.filter(file => {
		const { taxonomy_category, taxonomy_definition } = file;

		const isDataFile = taxonomy_category === DATA_TAXONOMY_CATEGORY_ID;
		const isExperienceFile =
			taxonomy_category === EXPERIENCE_TAXONOMY_CATEGORY_ID &&
			taxonomy_definition == pageRegistryTaxonomyId;

		return isDataFile || isExperienceFile;
	});
}
