import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';

// Wizard imports imports
import './pages/sn-mobile-details-page';
import './pages/sn-mobile-summary-page';
import styles from './sn-mobile-template-wizard.scss';

// Mobile flow definition
const MOBILE_DETAILS_PAGE = 'sn-mobile-details-page';
const MOBILE_SUMMARY_PAGE = 'sn-mobile-summary-page';

const MOBILE_FLOW = {
	startPage: MOBILE_DETAILS_PAGE,
	pages: {
		[MOBILE_DETAILS_PAGE]: {
			continue: () => MOBILE_SUMMARY_PAGE
		},

		[MOBILE_SUMMARY_PAGE]: {
			continue: () => {}
		}
	}
};

// Stepper items flow
const STEPPER_ITEMS = {
	[MOBILE_DETAILS_PAGE]: t('Basic Info'),
	[MOBILE_SUMMARY_PAGE]: t('Summary')
};

createCustomElement('sn-mobile-template-wizard', {
	view(state) {
		const { app, templateId, wizardContext, stepperItems } = state.properties;
		const PageComponent = getCurrentPage(wizardContext);

		return (
			<sn-guided-wizard
				flowDefinition={MOBILE_FLOW}
				component-id="mobileWizard"
				stepper-items={stepperItems}
				initial-context={{
					templateId,
					app
				}}
				tables-data-options={{
					appId: app.sys_id,
					fetchOnBootstrap: true,
					normalizeResult: result => {
						return result.map(table => {
							return {
								id: table.name,
								label: table.label,
								sublabel: table.name,
								is_extendable: table.is_extendable
							};
						});
					}
				}}
				roles-data-options={{
					appId: app.sys_id,
					fetchOnBootstrap: true,
					normalizeResult: result => {
						return result.map(role => {
							return {
								id: role.sys_id,
								label: role.name,
								description: role.description
							};
						});
					}
				}}>
				<PageComponent wizard-context={wizardContext} />
			</sn-guided-wizard>
		);
	},

	properties: {
		app: { default: {} },
		templateId: { default: null },
		wizardContext: { default: '@mobileWizard/wizardContext' }
	},

	behaviors: [
		{
			behavior: stepperBehavior.behavior,
			options: {
				config: STEPPER_ITEMS
			}
		}
	],

	styles
});
