import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import { t } from 'sn-translate';

import uniqueFieldBehavior from '../../../behaviors/unique-field';
import detailsValidationHelpers from '../../../helpers/details-validation-helpers';
import fieldValidationHelpers from '../../../helpers/field-validations';

const PREVENT_ONLY_WHITESPACE_REGEX = '(?!^ +$).*';

const {
	helpers: { checkUniqueField },
	actionTypes: { CHECK_UNIQUE_FIELD_COMPLETED }
} = uniqueFieldBehavior;
const { isAttrInvalid } = fieldValidationHelpers;
const { validateDetails } = detailsValidationHelpers;

import styles from './styles.scss';
import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

createCustomElement('sn-security-role-input-page', {
	view: (state, { dispatch }) => {
		const { name, description, isFormValid, errors } = state;

		function onPrimaryClick() {
			let role = {
				description,
				name
			};

			dispatch('SN_GUIDED_WIZARD#CONTINUE', { role });
		}

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onPrimaryClick}
				primaryDisabled={!isFormValid}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onPrevious={() => dispatch('SN_GUIDED_WIZARD#PREVIOUS')}
				running-header-text={t('ADD SECURITY')}
				header-text={t("Let's get started with a new role")}
				subheader-text={t('This role needs a name and description.')}
				maxWidth="680px">
				<div className="inputs">
					<now-input
						name="name"
						value={name}
						placeholder={t('Name this role')}
						label={t('Name')}
						helperContent={generateStyleToolTipContent(
							t('We recommend using file name syntax, like "manager_europe"')
						)}
						size="lg"
						autocomplete="off"
						required
						pattern={PREVENT_ONLY_WHITESPACE_REGEX}
						invalid={isAttrInvalid(errors, 'name')}
						messages={
							isAttrInvalid(errors, 'name')
								? [
										{
											status: 'critical',
											content: errors.name.message
										}
								  ]
								: []
						}
					/>
					<now-input
						name="description"
						value={description}
						placeholder={t('Describe this role')}
						label={t('Description')}
						helperContent={generateStyleToolTipContent(
							t(
								'Clear role descriptions can help an admin differentiate between the various roles that are part of this app.'
							)
						)}
						size="lg"
						autocomplete="off"
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {},
		app: {}
	},
	initialState: {
		name: '',
		description: '',
		errors: {},
		isFormValid: false,
		currentUniqueCheck: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_DOM_READY]: ({ dispatch }) => {
			dispatch('ENABLE_STEPPER');
		},
		['NOW_INPUT#VALUE_SET']: ({
			properties,
			action: { payload, stopPropagation },
			dispatch,
			updateState
		}) => {
			stopPropagation();

			const { value, name } = payload;
			const { wizardContext } = properties;
			updateState({
				[name]: value.trim()
			});

			if (name === 'name') {
				const scopedRoleName = `${wizardContext.app.scope}.${value}`;
				updateState({
					currentUniqueCheck: {
						fieldName: 'name',
						value: value.trim()
					}
				});
				checkUniqueField(
					dispatch,
					{
						tableName: 'sys_user_role',
						fieldName: 'name',
						value: scopedRoleName
					},
					wizardContext.app.sys_id
				);
			}
		},
		['NOW_INPUT#INVALID_SET']: ({
			state,
			action: {
				payload: { value, fieldValue, name },
				stopPropagation
			},
			dispatch
		}) => {
			stopPropagation();
			// Bail out if no change has been made
			if (state[name] === fieldValue) {
				return;
			}
			dispatch('GENERATE_ERROR_MESSAGE', {
				value,
				fieldValue: fieldValue.trim(),
				name
			});
		},
		GENERATE_ERROR_MESSAGE: ({
			action: {
				payload: { value, fieldValue, name, unique = true }
			},
			updateState,
			dispatch
		}) => {
			let message = null;

			if (value && !fieldValue) {
				message = t('Required');
			}

			if (value && !unique) {
				message = t('Role name already taken');
			}

			updateState({
				path: `errors.${name}`,
				value: { isDirty: true, message },
				operation: 'set'
			});

			dispatch('VALIDATE');
		},
		[CHECK_UNIQUE_FIELD_COMPLETED]: ({
			action: {
				payload: { unique }
			},
			state,
			dispatch
		}) => {
			dispatch('GENERATE_ERROR_MESSAGE', {
				value: true,
				fieldValue: state.currentUniqueCheck.value,
				name: state.currentUniqueCheck.fieldName,
				unique
			});
		},
		VALIDATE: ({ updateState }) => {
			updateState(({ state }) => {
				const isFormValid = validateDetails(state);
				return { isFormValid };
			});
		}
	},
	behaviors: [{ behavior: uniqueFieldBehavior.behavior }],
	styles
});
