import { createCustomElement } from '@servicenow/ui-core';
import { getCurrentPage } from '@devsnc/guided-wizard-toolkit';
import styles from './sn-application-template-wizard.scss';

// import pages
import './pages/sn-application-details-page';
import './pages/sn-application-summary-page';

// Application Flow Definition
const DETAILS_PAGE = 'sn-application-details-page';
const SUMMARY_PAGE = 'sn-application-summary-page';

export const applicationFlow = {
	startPage: DETAILS_PAGE,
	pages: {
		[DETAILS_PAGE]: {
			continue: () => SUMMARY_PAGE
		},
		[SUMMARY_PAGE]: { previous: () => DETAILS_PAGE }
	}
};
// End Application Flow Definition

createCustomElement('sn-application-template-wizard', {
	view: ({ properties }, { dispatch }) => {
		const {
			wizardContext,
			stepperItems,
			templateId,
			collectedResults
		} = properties;
		const PageComponent = getCurrentPage(wizardContext);
		return (
			<sn-guided-wizard
				flowDefinition={applicationFlow}
				component-id="wizard"
				stepper-items={stepperItems}
				initial-context={{
					templateId,
					onClose: () => dispatch('SN_GUIDED_WIZARD#CANCEL'),
					...collectedResults
				}}>
				<PageComponent wizard-context={wizardContext} />
			</sn-guided-wizard>
		);
	},
	properties: {
		wizardContext: { default: '@wizard/wizardContext' },
		templateId: { default: null },
		collectedResults: { default: {} }
	},
	styles
});
