import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import '@devsnc/guided-wizard-toolkit';
import hasMobileStudioAccess from '../../helpers/hasMobileStudioAccess';

import 'sn-app-object-wizard-components';

import mobileApplicationIcon from '../../../assets/mobile-application.svg';
import styles from './sn-mobile-summary-page.scss';

const getSubheaderText = hasMobileAccess => {
	return hasMobileAccess
		? t(
				'You can edit this mobile experience in Mobile Studio or continue building the rest of your app.'
		  )
		: t(
				'Your app is available, but cannot be modified. If you want to continue editing your mobile app, please contact your system administrator.'
		  );
};

createCustomElement('sn-mobile-summary-page', {
	view(
		{
			properties: { wizardContext, currentInstanceAesConfig }
		},
		helpers
	) {
		const { dispatch } = helpers;

		const devStudioLink = `/$studio.do?sysparm_transaction_scope=${
			wizardContext.app.sys_id
		}`;

		const hasMobileAccess = hasMobileStudioAccess(currentInstanceAesConfig);

		const subheader = getSubheaderText(hasMobileAccess);

		const data = [
			{
				icon: mobileApplicationIcon,
				label: t('Icon of role'),
				name: t('Two icons of people overlapping')
			},
			{
				label: t('Name'),
				text: wizardContext.mobileTemplateVariables.name,
				maxCharLength: 13
			},
			{
				label: t('Type'),
				text: t('Mobile'),
				maxCharLength: 23
			}
		];

		const onComplete = () =>
			dispatch('SN_GUIDED_WIZARD#COMPLETE', {
				key: 'mobile_app',
				result: []
			});

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onComplete}
				onSecondaryClick={
					hasMobileAccess
						? () => {
								window.open(devStudioLink, '_blank');
								dispatch('SN_GUIDED_WIZARD#COMPLETE', {
									key: 'mobile_app',
									result: []
								});
						  }
						: null
				}
				secondaryLabel={t('Edit')}
				primaryLabel={t('Done')}
				onClose={onComplete}
				runningHeaderText={t('ADD EXPERIENCE')}
				headerText={t('Success! A mobile experience has been added')}
				subheaderText={subheader}
				max-width="640px">
				<sn-aow-info-summary data={data} />
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {},
		currentInstanceAesConfig: {
			default: '@sn-aes-provider/currentInstanceAesConfig'
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#COMPLETE_ALL');
		}
	},
	styles
});
