export function getAllCheckedStatus(count, upper = 4) {
	return count === 0 ? false : count === upper || 'indeterminate';
}

export function createAcl(name, type, operation) {
	const acl = {
		type,
		name
	};

	if (operation) {
		acl.operation = operation;
	}

	return acl;
}

export function flatten(collection, mapper = item => item) {
	return collection.reduce((acc, item) => acc.concat(mapper(item)), []);
}

export function extractAclsFromDefinition(aclDefinition) {
	let { acls } = aclDefinition;

	if (!Array.isArray(acls)) {
		acls = Object.values(acls);
	}

	return acls.filter(acl => acl != null);
}

// Matches on:
// 1. The first slash in '/now/example/url/path'
// 2. The last slash in 'now/example/url/path/'
// 3. Both slashes in '/now/example/url/path/'
const TRAILING_URL_SLASHES_REGEXP = /^\/|\/$/g;
const INTERNAL_URL_SLASHES_REGEXP = /\//g;

/**
 * Transforms the url `path` from sys_ux_page_registry to conform to `ux_page` and `ux_route` acl name format
 *
 * @example
 * transformUrlPath('/now/example/url/path');
 * // => 'now.example.url.path'
 *
 * @param {string} path
 * @returns {string}
 */
export function transformUrlPath(path) {
	return path
		.replace(TRAILING_URL_SLASHES_REGEXP, '')
		.replace(INTERNAL_URL_SLASHES_REGEXP, '.');
}
