import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import '@devsnc/guided-wizard-toolkit';

import { t } from 'sn-translate';

import 'sn-app-object-wizard-components';

import styles from './styles.scss';

import roleIcon from '../../../assets/rolesicon.png';

createCustomElement('sn-security-summary-page', {
	view: (
		{
			properties: {
				appHasValidObjects,
				wizardContext: {
					app,
					securityTemplateResult: { outputs }
				}
			}
		},
		{ dispatch }
	) => {
		const result = {};

		outputs.forEach(el => {
			const { name, value } = el;
			// replacing the 'role_' from name
			result[name.replace(/^role_/, '')] = value;
		});

		const data = [
			{
				icon: roleIcon,
				label: t('Icon of role'),
				name: t('Two icons of people overlapping')
			},
			{
				label: t('Role name'),
				text: result.suffix || result.name,
				maxCharLength: 13
			},
			{
				label: t('Description'),
				text: result.description,
				maxCharLength: 23
			}
		];

		const subheaderTextAppHasValidObjects = t(
			'You can edit the role you just added or continue building the rest of your app.'
		);

		const subheaderTextAppHasNoValidObjects = t(
			"Since you dont have any items in the {0}, you can't yet configure your permissions for this role. You can edit this role, or choose 'Done' to continue building the rest of your app.",
			app.name
		);

		const onComplete = () =>
			dispatch('SN_GUIDED_WIZARD#COMPLETE', {
				key: 'sys_user_role',
				result
			});

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onComplete}
				primaryLabel={t('Done')}
				secondaryLabel={t('Edit')}
				onSecondaryClick={() =>
					dispatch('SN_GUIDED_WIZARD#EDIT_RECORD', {
						key: 'sys_user_role',
						result
					})
				}
				onClose={onComplete}
				running-header-text={t('ADD SECURITY')}
				header-text={t('Success! This role has been added to your app')}
				subheader-text={
					appHasValidObjects
						? subheaderTextAppHasValidObjects
						: subheaderTextAppHasNoValidObjects
				}
				maxWidth="680px">
				<sn-aow-info-summary data={data} />
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {},
		appHasValidObjects: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#COMPLETE_ALL');
		}
	},
	styles
});
