import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { getComponentsBySysIds } from '@devsnc/library-uxf';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import '@servicenow/now-loader';
import uuid from '../helpers/uuid';
import {
	LOAD_COMPONENT,
	TEMPLATE_COMPLETE,
	TEMPLATE_EDIT_RECORD,
	TEMPLATE_CANCEL,
	TEMPLATE_REPEAT,
	FETCH_TEMPLATE,
	FETCH_TEMPLATE_SUCCESS,
	FETCH_TEMPLATE_FAILURE,
	FETCH_APP,
	FETCH_APP_SUCCESS,
	FETCH_APP_FAILURE,
	INIT_COMPONENT,
	COLLECTED_RESULTS_SET,
	INIT_NEXT_SUBTEMPLATE,
	RUN_PARENT_TEMPLATE,
	RUN_PARENT_TEMPLATE_SUCCESS,
	RUN_PARENT_TEMPLATE_FAILURE,
	TEMPLATE_WARNING,
	TEMPLATE_FAILURE,
	TEMPLATE_DIRTY_SET
} from './constants';
import styles from './sn-template-manager.scss';

export const INVALID_TEMPLATE_ERROR = {
	status: '404',
	message: t('Error loading template wizard'),
	detail: t(
		'This template and its subsequent template does not have a uxf_component. Contact your system administrator to address this issue.'
	)
};

// The purpose of this UXF Component is to provide parent applications a component to load
// that can render wizards via the `uxf_component` field of a template definition.
// If a template has subtemplates, this component will render them sequentially.
// When all subtemplates have been completed, a SN_TEMPLATE_MANAGER#TEMPLATE_COMPLETE action will emit

export const throwError = ({ action: { payload } }) => {
	console.warn(payload.message);
};

const apiCritFailEffect = ({
	action: {
		payload: {
			data: { error }
		}
	},
	dispatch
}) => {
	dispatch(TEMPLATE_FAILURE, error);
};

export const actionHandlers = {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
		properties: { templateId },
		dispatch
	}) => {
		dispatch(FETCH_TEMPLATE, { templateId, style: 'lite' });
	},
	[FETCH_TEMPLATE]: createHttpEffect('/api/now/templates/:templateId', {
		method: 'GET',
		successActionType: FETCH_TEMPLATE_SUCCESS,
		errorActionType: FETCH_TEMPLATE_FAILURE,
		batch: false,
		pathParams: 'templateId',
		queryParams: ['style']
	}),

	[FETCH_APP]: createHttpEffect('/api/now/table/sys_app/:appId', {
		method: 'GET',
		successActionType: FETCH_APP_SUCCESS,
		errorActionType: FETCH_APP_FAILURE,
		batch: false,
		pathParams: 'appId'
	}),

	/**
	 * If a template does not have a wizard and does have subtemplates,
	 * the template runner needs to run in order to store the parent template instance id
	 */
	[RUN_PARENT_TEMPLATE]: createHttpEffect('/api/now/templates', {
		method: 'POST',
		successActionType: RUN_PARENT_TEMPLATE_SUCCESS,
		errorActionType: RUN_PARENT_TEMPLATE_FAILURE,
		batch: false,
		dataParam: 'templateRequest',
		queryParams: ['sysparm_transaction_scope']
	}),

	[FETCH_TEMPLATE_SUCCESS]: ({
		properties: { appId },
		action,
		updateState,
		dispatch
	}) => {
		const template = action.payload.result;
		const subtemplates = template.subtemplates || [];
		const isInvalidTemplate =
			!template.uxf_component && subtemplates.length === 0;
		// If the template has no uxf_component, or subtemplates
		// critical fail the wizard
		if (isInvalidTemplate) {
			dispatch(TEMPLATE_FAILURE, INVALID_TEMPLATE_ERROR);
			return;
		}
		updateState({ template: { ...template } });
		appId ? dispatch(FETCH_APP, { appId }) : dispatch(INIT_COMPONENT);
	},

	[FETCH_APP_SUCCESS]: ({ action: { payload }, dispatch }) => {
		dispatch(COLLECTED_RESULTS_SET, {
			key: 'sys_app',
			result: payload.result
		});
		dispatch(INIT_COMPONENT);
	},

	[RUN_PARENT_TEMPLATE_SUCCESS]: ({ action: { payload }, dispatch }) => {
		dispatch('SN_GUIDED_WIZARD#PARENT_INSTANCE_ID_SET', payload);
		dispatch(INIT_NEXT_SUBTEMPLATE);
	},

	[FETCH_TEMPLATE_FAILURE]: apiCritFailEffect,
	[FETCH_APP_FAILURE]: apiCritFailEffect,
	[RUN_PARENT_TEMPLATE_FAILURE]: apiCritFailEffect,

	[INIT_COMPONENT]: ({ state: { template }, dispatch }) => {
		const initialComponentId = template.uxf_component;
		if (!initialComponentId) {
			/**
			 * When there is no uxf component, we assume there is no flow associated with
			 * the current template and run it as a no-op template so that the parent instance id
			 * can be tracked throughout the subsequent subtemplates
			 */
			dispatch(RUN_PARENT_TEMPLATE, {
				templateRequest: {
					template_sys_id: template.sys_id,
					variables: {}
				}
			});
		} else {
			dispatch(LOAD_COMPONENT, {
				uxfComponentId: initialComponentId,
				templateId: template.sys_id
			});
		}
	},

	[LOAD_COMPONENT]: async ({
		action: {
			payload: { uxfComponentId, templateId }
		},
		updateState
	}) => {
		const [Component] = await getComponentsBySysIds([uxfComponentId]);
		updateState({
			activeUXFComponent: {
				Component,
				key: uuid()
			},
			templateId
		});
	},

	// Store results throughout template/subtemplates sequence
	// TODO: Store entries other than sys_app as arrays
	[COLLECTED_RESULTS_SET]: ({
		properties: { collectedResults },
		action: {
			payload: { key, result }
		},
		updateProperties
	}) => {
		if (!key) {
			console.warn('Payload must include a key');
			return;
		}

		const output = { ...collectedResults };
		if (key === 'sys_app') {
			output.sys_app = result;
		} else {
			output[key] = (collectedResults[key] &&
				collectedResults[key].concat(result)) || [result];
		}
		updateProperties({
			collectedResults: output
		});
	},

	[INIT_NEXT_SUBTEMPLATE]: ({
		state: { template, subtemplateIndex },
		properties: { collectedResults },
		updateState,
		dispatch
	}) => {
		const subtemplates = template.subtemplates || [];
		if (subtemplateIndex < subtemplates.length) {
			const nextSubtemplate = subtemplates[subtemplateIndex].template;
			const { uxf_component = null, sys_id } = nextSubtemplate;

			// Subtemplates must have a wizard
			if (!uxf_component) {
				dispatch(TEMPLATE_FAILURE, INVALID_TEMPLATE_ERROR);
				return;
			}

			dispatch(LOAD_COMPONENT, {
				uxfComponentId: uxf_component,
				templateId: sys_id
			});

			updateState({ subtemplateIndex: subtemplateIndex + 1 });
		} else {
			dispatch(TEMPLATE_COMPLETE, { ...collectedResults });
		}
	},

	[TEMPLATE_REPEAT]: ({ state: { activeUXFComponent }, updateState }) => {
		const { Component } = activeUXFComponent;
		updateState({ activeUXFComponent: { Component, key: uuid() } });
	},

	// Once the wizard is complete, check if there are more subtemplates
	// and if there are, load the next one, if there aren't emit TEMPLATE_COMPLETE
	'SN_GUIDED_WIZARD#COMPLETE': ({ action: { payload }, dispatch }) => {
		dispatch(COLLECTED_RESULTS_SET, payload);
		dispatch(INIT_NEXT_SUBTEMPLATE);
	},
	'SN_GUIDED_WIZARD#REPEAT': ({ action: { payload }, dispatch }) => {
		dispatch(COLLECTED_RESULTS_SET, payload);
		dispatch(TEMPLATE_REPEAT);
	},
	'SN_GUIDED_WIZARD#CANCEL': ({ action: { payload }, dispatch }) => {
		dispatch(TEMPLATE_CANCEL, { payload });
	},
	'SN_GUIDED_WIZARD#EDIT_RECORD': ({
		properties: { collectedResults },
		action: {
			payload: { key = null, result }
		},
		dispatch
	}) => {
		dispatch(TEMPLATE_EDIT_RECORD, {
			key,
			sys_app: collectedResults.sys_app,
			result: Array.isArray(result) ? result : [result]
		});
	},
	'SN_GUIDED_WIZARD#PARENT_INSTANCE_ID_SET': ({
		state: { parent_template_instance_id },
		action: { payload },
		updateState
	}) => {
		const id = payload.instanceId || payload.result;
		if (!parent_template_instance_id && id) {
			updateState({ parent_template_instance_id: id });
		}
	},
	'SN_GUIDED_WIZARD#REQUEST_WARNING': ({ action: { payload }, dispatch }) => {
		// TODO: Show more human readable errors, may require parsing the errors returned from API
		dispatch(TEMPLATE_WARNING, payload);
	},
	'SN_GUIDED_WIZARD#REQUEST_CRITICAL': ({ action: { payload }, dispatch }) => {
		// TODO: Show more human readable errors, may require parsing the errors returned from API
		dispatch(TEMPLATE_FAILURE, payload);
	},
	'RUN_TEMPLATE_BEHAVIOR#RUN_TEMPLATE_WARNING': ({
		action: { payload },
		dispatch
	}) => {
		dispatch(TEMPLATE_WARNING, payload);
	},
	'RUN_TEMPLATE_BEHAVIOR#RUN_TEMPLATE_FAILURE': ({
		action: { payload },
		dispatch
	}) => {
		dispatch(TEMPLATE_FAILURE, payload);
	},
	'SN_GUIDED_WIZARD#CONTINUE': ({ dispatch }) => {
		dispatch(TEMPLATE_DIRTY_SET);
	}
};

createCustomElement('sn-template-manager', {
	view: ({
		activeUXFComponent,
		templateId,
		parent_template_instance_id,
		loading,
		properties: { collectedResults }
	}) => {
		const { Component, key } = activeUXFComponent;

		if (!Component || !templateId || loading) {
			return <now-loader />;
		}

		return (
			<Component
				key={key}
				templateId={templateId}
				app={{ ...collectedResults.sys_app }}
				collected-results={collectedResults}
				parentTemplateInstanceId={parent_template_instance_id}
			/>
		);
	},
	initialState: {
		error: {},
		activeUXFComponent: {
			Component: null,
			key: null
		},
		template: {},
		subtemplateIndex: 0,
		parent_template_instance_id: null
	},
	properties: {
		templateId: { default: null },
		appId: { default: null },
		collectedResults: { default: { sys_app: null } }
	},
	actionHandlers,
	dispatches: {
		'SN_TEMPLATE_MANAGER#TEMPLATE_COMPLETE': {},
		'SN_TEMPLATE_MANAGER#TEMPLATE_CANCEL': {},
		'SN_TEMPLATE_MANAGER#TEMPLATE_REPEAT': {},
		'SN_TEMPLATE_MANAGER#EDIT_RECORD': {},
		'SN_TEMPLATE_MANAGER#TEMPLATE_WARNING': {},
		'SN_TEMPLATE_MANAGER#TEMPLATE_FAILURE': {}
	},
	styles
});
