import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-checkbox';

import {
	TAB_ACLS_SET,
	CRUD_SELECT_ALL_ACLS,
	CRUD_REMOVE_ALL_ACLS,
	CRUD_SELECT_ALL_FILE_ACLS,
	CRUD_REMOVE_ALL_FILE_ACLS,
	CRUD_SELECT_FILE_ACL,
	CRUD_REMOVE_FILE_ACL
} from './constants';

import { createAcl, getAllCheckedStatus } from './helpers';

import styles from './tab-panel-shared.scss';

const CRUD_ARIA_LABELS = {
	create: t('Create'),
	read: t('Read'),
	write: t('Write'),
	delete: t('Delete')
};

const getAllDefinitionsCheckedStatus = aclDefinitions => {
	let checkedCount = 0;

	for (const aclDefinition of aclDefinitions) {
		const status = getAllCheckedStatus(aclDefinition.count);

		if (typeof status === 'string') {
			return status;
		}

		if (status) {
			checkedCount++;
		}
	}

	return getAllCheckedStatus(checkedCount, aclDefinitions.length);
};

function castCrudAclDefinition(objectFile, aclType) {
	return {
		id: objectFile.sys_id,
		name: objectFile.name,
		sys_name: objectFile.name_value,
		...(typeof aclType === 'string'
			? {
					count: 4,
					acls: {
						create: createAcl(objectFile.name_value, aclType, 'create'),
						read: createAcl(objectFile.name_value, aclType, 'read'),
						write: createAcl(objectFile.name_value, aclType, 'write'),
						delete: createAcl(objectFile.name_value, aclType, 'delete')
					}
			  }
			: {
					count: 0,
					acls: {
						create: null,
						read: null,
						write: null,
						delete: null
					}
			  })
	};
}

createCustomElement('sn-security-crud-acl-tab-panel', {
	view(state, { dispatch }) {
		const { aclDefinitions, properties } = state;
		const { tabName, tabLabel } = properties;

		// To keep data congruent and not iterate over everything a second time,
		// collect the raw acls while rendering and dispatching that to sn-security-define-acl-page
		dispatch(TAB_ACLS_SET, { tabName, aclDefinitions });

		return (
			<ul className={`object-list object-list--crud`}>
				{aclDefinitions.length > 0 ? (
					<li className="object-list-item -header">
						<div className="item-cell -name">
							<now-checkbox
								checked={getAllDefinitionsCheckedStatus(aclDefinitions)}
								manage-checked
								config-aria={{
									'aria-label': t('Grant all ACLs to {0}', tabLabel)
								}}
								append-to-payload={{
									operation: 'allAcls'
								}}
							/>
							{t('Select All')}
						</div>
						<div className="item-cell -create">{CRUD_ARIA_LABELS.create}</div>
						<div className="item-cell -read">{CRUD_ARIA_LABELS.read}</div>
						<div className="item-cell -write">{CRUD_ARIA_LABELS.write}</div>
						<div className="item-cell -delete">{CRUD_ARIA_LABELS.delete}</div>
					</li>
				) : (
					<p>
						{t(
							'You cannot configure these permissions until you add data to this app.'
						)}
					</p>
				)}

				{aclDefinitions.map((aclDefinition, index) => {
					const aclPayload = {
						aclDefinition,
						index
					};

					function renderCheckbox(operation) {
						const acl = aclDefinition.acls[operation];

						return (
							<now-checkbox
								checked={Boolean(acl)}
								manage-checked
								config-aria={{
									// These are already considered translated, so no `t()`
									// besides, these terms don't make up a cohesive thought, they're more tags for the screen to read
									'aria-label': `${aclDefinition.name} ${
										CRUD_ARIA_LABELS[operation]
									}`
								}}
								append-to-payload={{
									...aclPayload,
									operation
								}}
							/>
						);
					}

					return (
						<li
							key={aclDefinition.id}
							className="object-list-item"
							sys-id={aclDefinition.id}>
							<div className="item-cell -name">
								<now-checkbox
									checked={getAllCheckedStatus(aclDefinition.count)}
									manage-checked
									config-aria={{
										'aria-label': t(
											'Select all ACLs for {0}',
											aclDefinition.name
										)
									}}
									append-to-payload={{
										...aclPayload,
										operation: 'allFileAcls'
									}}
								/>
								{aclDefinition.name}
							</div>
							<div className="item-cell -create">
								{renderCheckbox('create')}
							</div>
							<div className="item-cell -read">{renderCheckbox('read')}</div>
							<div className="item-cell -write">{renderCheckbox('write')}</div>
							<div className="item-cell -delete">
								{renderCheckbox('delete')}
							</div>
						</li>
					);
				})}
			</ul>
		);
	},

	initialState: {
		aclDefinitions: []
	},

	properties: {
		aclType: { default: null },
		tabName: { default: null },
		tabLabel: { default: null },
		objectFiles: { default: [] },

		// Strictly used on component connected to retain tab state when tabbing away
		aclDefinitions: { default: [] }
	},

	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: ({
			properties,
			dispatch,
			updateState
		}) => {
			const { aclDefinitions } = properties;

			if (aclDefinitions.length > 0) {
				updateState({ aclDefinitions });
			} else {
				dispatch(CRUD_REMOVE_ALL_ACLS);
			}
		},

		['NOW_CHECKBOX#CHECKED_SET']: ({ action, dispatch }) => {
			action.stopPropagation();

			const { value: checked, operation } = action.payload;
			const aliasAction = actionType =>
				dispatch(actionType, action.payload, action.meta);

			switch (operation) {
				case 'allAcls':
					aliasAction(checked ? CRUD_SELECT_ALL_ACLS : CRUD_REMOVE_ALL_ACLS);
					break;

				case 'allFileAcls':
					aliasAction(
						checked ? CRUD_SELECT_ALL_FILE_ACLS : CRUD_REMOVE_ALL_FILE_ACLS
					);
					break;

				default:
					aliasAction(checked ? CRUD_SELECT_FILE_ACL : CRUD_REMOVE_FILE_ACL);
					break;
			}
		},

		[CRUD_SELECT_ALL_ACLS]: ({ properties, updateState }) => {
			const { objectFiles, aclType } = properties;
			const aclDefinitions = objectFiles.map(file =>
				castCrudAclDefinition(file, aclType)
			);
			updateState({ aclDefinitions });
		},

		[CRUD_REMOVE_ALL_ACLS]: ({ properties, updateState }) => {
			const { objectFiles } = properties;
			const aclDefinitions = objectFiles.map(file =>
				castCrudAclDefinition(file)
			);
			updateState({ aclDefinitions });
		},

		[CRUD_SELECT_ALL_FILE_ACLS]: ({ properties, action, updateState }) => {
			const { aclType, objectFiles } = properties;
			const { index } = action.payload;
			const file = objectFiles[index];

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}]`,
				value: castCrudAclDefinition(file, aclType)
			});
		},

		[CRUD_REMOVE_ALL_FILE_ACLS]: ({ properties, action, updateState }) => {
			const { objectFiles } = properties;
			const { index } = action.payload;
			const file = objectFiles[index];

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}]`,
				value: castCrudAclDefinition(file)
			});
		},

		[CRUD_SELECT_FILE_ACL]: ({ properties, action, updateState }) => {
			const { aclType } = properties;
			const {
				operation,
				index,
				aclDefinition: { sys_name, count }
			} = action.payload;

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}].acls.${operation}`,
				value: createAcl(sys_name, aclType, operation)
			});

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}].count`,
				value: count + 1
			});
		},

		[CRUD_REMOVE_FILE_ACL]: ({ action, updateState }) => {
			const {
				operation,
				index,
				aclDefinition: { count }
			} = action.payload;

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}].acls.${operation}`,
				value: null
			});

			updateState({
				operation: 'set',
				path: `aclDefinitions[${index}].count`,
				value: count - 1
			});
		}
	},

	styles
});
