import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import '@servicenow/now-input';

import { generateStyleToolTipContent } from 'sn-app-object-wizard-components';

import uploadImageEffect from '../../../helpers/upload-image-effect';
import isScopedAppTemplate from '../../helpers/isScopedAppTemplate';

import {
	SN_APP_WIZARD__CREATE_APPLICATION,
	SN_APP_WIZARD__REFRESH_USER_SESSION,
	SN_APP_WIZARD__REFRESH_USER_SESSION_SUCCESS,
	SN_APP_WIZARD__REFRESH_USER_SESSION_FAILURE,
	PREVENT_ONLY_WHITESPACE_REGEX
} from './constants';

import progressPagePeople from './assets/progress-page-people.png';
import styles from './sn-application-details-page.scss';

import {
	validationBehavior,
	runTemplateBehavior
} from '@devsnc/guided-wizard-toolkit';
const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

const {
	helpers: { runTemplate },
	actionTypes: { RUN_TEMPLATE_SUCCESS, RUN_TEMPLATE_WARNING }
} = runTemplateBehavior;

const headerTextCopy = {
	scratch: t("Let's get started on your new app"),
	template: t("Let's use this template to create your new app")
};

const subheaderTextCopy = {
	scratch: t(
		'Add a name and description that define the purpose of your app. You can also add a logo to represent your app.'
	),
	template: t(
		'Add a name and a description to make this app your own. You can also add a logo to represent your app.'
	)
};

createCustomElement('sn-application-details-page', {
	view: (state, { dispatch }) => {
		const {
			fileUpload,
			name,
			short_description,
			isFormValid,
			isCreating
		} = state;
		const { wizardContext } = state.properties;
		// const error = wizardContext.error;
		const { templateId } = wizardContext;
		const textCondition = isScopedAppTemplate(templateId)
			? 'scratch'
			: 'template';

		return (
			<sn-guided-wizard-page
				name="application-details"
				onPrimaryClick={() => {
					dispatch(SN_APP_WIZARD__CREATE_APPLICATION, {
						app: {
							name,
							short_description
						}
					});
				}}
				primary-disabled={!isFormValid || isCreating}
				onSecondaryClick={wizardContext.onClose}
				onClose={wizardContext.onClose}
				secondary-label={t('Cancel')}
				runningHeaderText={t('CREATE APP')}
				headerText={headerTextCopy[textCondition]}
				subheaderText={subheaderTextCopy[textCondition]}
				max-width="640px"
				loading={isCreating}
				loadingContent={{
					runningHeaderText: t('CREATE APP'),
					headerText: t(`Sit tight while we create this app`),
					subheaderText: t('This may take a few minutes...'),
					image: progressPagePeople
				}}>
				<div className="body-container">
					<div className="left-column">
						<now-input
							name="name"
							value={name}
							placeholder={t('Name this app')}
							label={t('Name')}
							helperContent={generateStyleToolTipContent(
								t(
									"This name applies to the container and also shows up in the ServiceNow App store. It's not the name of a Workspace or Mobile application. It can't be more than 100 characters long."
								)
							)}
							size="lg"
							autocomplete="off"
							maxlength={100}
							required
							pattern={PREVENT_ONLY_WHITESPACE_REGEX}
						/>
						<now-input
							name="short_description"
							value={short_description}
							placeholder={t('Describe this app')}
							label={t('Description')}
							helperContent={generateStyleToolTipContent(
								t(
									"This description shows up in the ServiceNow App store. It can't be more than 500 characters long."
								)
							)}
							size="lg"
							maxlength={500}
							autocomplete="off"
						/>
						{/* {error ? <p className="error">{error.data.error.message}</p> : null} */}
					</div>
					<sn-aow-file-uploader-image
						label={t('Drag app logo or browse to upload')}
						ref={fileUpload.ref}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		isCreating: false,
		fileUpload: {
			hasSelectedFile: false,
			ref: createRef()
		},
		name: '',
		short_description: '',
		isFormValid: false
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	properties: {
		wizardContext: {},
		app: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ properties, updateState }) => {
			const { wizardContext } = properties;
			if (wizardContext.app) {
				updateState({ ...wizardContext.app });
			}
		},
		['AOW_FILE_UPLOADER#FILE_SET']: ({
			state: { fileUpload },
			action: { payload },
			updateState
		}) => {
			updateState({
				fileUpload: {
					...fileUpload,
					selectedFile: payload,
					hasSelectedFile: !!payload
				}
			});
		},
		['AOW_FILE_UPLOADER#FILE_REMOVE']: ({ updateState }) => {
			updateState({
				fileUpload: {
					selectedFile: null,
					hasSelectedFile: false
				}
			});
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		[SN_APP_WIZARD__CREATE_APPLICATION]: ({
			action: {
				payload: { app }
			},
			properties: {
				wizardContext: { templateId }
			},
			dispatch,
			updateState
		}) => {
			const appData = {
				application_name: app.name,
				short_description: app.short_description
			};
			runTemplate(dispatch, templateId, appData);
			updateState({ isCreating: true });
		},
		[RUN_TEMPLATE_SUCCESS]: ({
			state: { behaviors },
			action: {
				payload: { result }
			},
			dispatch,
			updateState
		}) => {
			const outputForApp = result.outputs.find(r => r.name === 'app_sys_id');

			if (!outputForApp.value) {
				console.warn('No output for application found');
				return;
			}

			const app = {};
			app.sys_id = outputForApp.value;
			updateState({ app }); // Set the app on state for a later chained action

			// Ensure the parent template is stored within an application template
			dispatch('SN_GUIDED_WIZARD#PARENT_INSTANCE_ID_SET', {
				instanceId: behaviors[runTemplateBehavior.name].templateInstanceId
			});

			dispatch(SN_APP_WIZARD__REFRESH_USER_SESSION, { app_sys_id: app.sys_id });
		},
		[SN_APP_WIZARD__REFRESH_USER_SESSION]: createHttpEffect(
			'/api/now/templates/refreshDelegatedDevRoles',
			{
				method: 'GET',
				successActionType: SN_APP_WIZARD__REFRESH_USER_SESSION_SUCCESS,
				errorActionType: SN_APP_WIZARD__REFRESH_USER_SESSION_FAILURE,
				batch: false,
				queryParams: ['app_sys_id']
			}
		),
		[SN_APP_WIZARD__REFRESH_USER_SESSION_SUCCESS]: ({
			state: { app, fileUpload },
			dispatch,
			updateState
		}) => {
			if (fileUpload.hasSelectedFile && app) {
				dispatch('UPLOAD_LOGO', {
					sys_id: app.sys_id,
					selectedFile: fileUpload.selectedFile
				});
			}
			updateState({ completed: true });
			dispatch('SN_GUIDED_WIZARD#CONTINUE', { app });
		},
		[SN_APP_WIZARD__REFRESH_USER_SESSION_FAILURE]: ({
			dispatch,
			state: { app },
			updateState
		}) => {
			updateState({ isCreating: false });

			dispatch('SN_GUIDED_WIZARD#REQUEST_CRITICAL', {
				detail: t(
					'Unable to refresh delegated developer roles for app with sys_id: "{0}". Contact your system administrator',
					app.sys_id
				)
			});
		},
		UPLOAD_LOGO: uploadImageEffect,
		[RUN_TEMPLATE_WARNING]: ({ updateState }) => {
			// This is the only case where we reset `isCreating` to allow user re-input
			updateState({ isCreating: false });
		}
	},
	styles,
	behaviors: [createValidationBehavior(['name']), runTemplateBehavior.behavior]
});
